@import "_detailCSS.css";

$mainColor : #eeeeee;
@mixin alert(){
  background : #eeeeee;
  padding : 15px;
  border-radius : 5px;
  max-width : 500px;
  width : 100%;
  margin : auto;
}

p{
  color : $mainColor;
}

div.container {
  h4 {
    color : peru;
  }
  p {
    color : antiquewhite;
  }
}

.my-alert {
 @include alert;
}
.my-alert p {
  margin-bottom : 0;
  color : brown;
}

.my-alert2 {
  @extend .my-alert;
  background : peru;
}

.itemDetailTab-enter{
  opacity : 0;
}

.itemDetailTab-enter-active{
  opacity : 1;
  transition : all 500ms;
}